* {
  padding: 0;
  margin: 0;
}

.userCard {
  width: 300px;
  border: 1px solid rgb(107, 107, 107);
  border-radius: 10px;
  margin-top: 30px;
}

.userInfo {
  padding: 20px;
  text-align: center;
}

.avatar {
  width: 150px;
  border-radius: 50%;
  margin-bottom: 20px;
  background-color: rgb(89, 170, 195);
}

.username {
  font-size: large;
  font-weight: 700;
  margin-bottom: 10px;
}

.tag {
  color: gray;
  margin-bottom: 10px;
}

.location {
  color: gray;
  margin-bottom: 10px;
}

.statList {
  display: flex;
  list-style: none;
  margin: 0;
  background-color: rgb(224, 224, 224);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.statItem {
  flex-basis: calc(100% / 3);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 5px;
  border-top: 1px solid grey;
  color: rgb(98, 98, 98);
}

.statItem:not(:last-child) {
  border-right: 1px solid grey;
}

.statValue {
  margin-top: 5px;
  color: black;
  font-size: large;
  font-weight: 700;
}
